"use strict";
var Config = require('../../config');
var NavBar = (function () {
    function NavBar() {
        var _this = this;
        this.elem = document.getElementById(Config.NAVELEM);
        this.menu = document.getElementById(Config.MENU);
        this.toggle = this.elem.querySelector('.toggle');
        this.toggle.addEventListener('click', function () { return _this.toggleNav(); });
        window.addEventListener('hashchange', function () {
            _this.menu.classList.remove('fixed');
        });
    }
    NavBar.prototype.toggleNav = function () {
        this.menu.classList.toggle('fixed');
    };
    return NavBar;
}());
module.exports = NavBar;



