"use strict";
require('../style/main.less');
var Router = require('./router/router');
var PostListFactory = require('./components/post/postListFactory');
var PageFactory = require('./components/page/pageFactory');
var StaticView = require('./components/staticPage/staticView');
var homeTemplate = require('./components/staticPage/homeTemplate');
var staticTemplate = require('./components/staticPage/staticTemplate');
var NavBar = require('./components/navBar/navBar');
var ScrollToTop = require('./components/common/scrollToTop');
var scrollPosition = require('./services/scroll/scrollPosition');
var postListFactory = new PostListFactory();
var pageFactory = new PageFactory();
var homeView = new StaticView(homeTemplate);
var staticView = new StaticView(staticTemplate);
var router = new Router();
var navBar = new NavBar();
ScrollToTop.init('scrollToTop');
// set default route to home if no route is present
if (!window.location.hash) {
    window.location.hash = '#/';
}
router.register('/', function (params) {
    onRouteChange(homeView);
    homeView.getPage();
})
    .register('/static', function (params) {
    var postList = postListFactory.getpostList(params);
    onRouteChange(staticView);
    staticView.getPage();
}).register('/posts', function (params) {
    params.category = 'all';
    var postList = postListFactory.getpostList(params);
    onRouteChange(postList);
    postList.getPosts(params);
})
    .register('/posts/:category', function (params) {
    var postList = postListFactory.getpostList(params);
    onRouteChange(postList);
    postList.getPosts(params);
})
    .register('/post/:category/:id', function (params) {
    var postList = postListFactory.getpostList(params);
    postList.getSinglePost(params);
})
    .register('/page/:id', function (params) {
    var pageView = pageFactory.getPageView(params);
    onRouteChange(pageView);
    pageView.getPage(params);
});
function onRouteChange(view) {
    scrollPosition.set(0);
    pageFactory.resetActive();
    postListFactory.resetActive();
    view.active = true;
}
